"use client"

import { useState, useEffect } from "react"
import {
  ChevronLeft,
  ChevronRight,
  Plus,
  Menu,
  CalendarDays,
  ListTodo,
  Inbox,
  Bot,
  User,
  Settings as SettingsIcon,
  LogOut,
} from "lucide-react"
import CalendarView from "@/components/dashboard/calendar-view"
import TimeBlockingModal from "@/components/dashboard/time-blocking-modal"

type TimeBlock = {
  id: string
  title: string
  date: string
  startTime: string
  endTime: string
  description?: string
}

type PresetBlock = {
  date: string
  startTime: string
  endTime: string
}

export default function DashboardPage() {
  const [currentDate, setCurrentDate] = useState(new Date())
  const [selectedDate, setSelectedDate] = useState<Date | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [timeBlocks, setTimeBlocks] = useState<TimeBlock[]>([])
  const [isSidebarOpen, setIsSidebarOpen] = useState(true)
  const [presetBlock, setPresetBlock] = useState<PresetBlock | null>(null)

  // Load time blocks from localStorage
  useEffect(() => {
    const stored = localStorage.getItem("timeBlocks")
    if (stored) {
      setTimeBlocks(JSON.parse(stored))
    }
  }, [])

  // Save time blocks to localStorage
  useEffect(() => {
    localStorage.setItem("timeBlocks", JSON.stringify(timeBlocks))
  }, [timeBlocks])

  const handleAddBlock = (block: any) => {
    setTimeBlocks([...timeBlocks, { ...block, id: Date.now().toString() }])
    setIsModalOpen(false)
    setPresetBlock(null)
  }

  const handleDeleteBlock = (id: string) => {
    setTimeBlocks(timeBlocks.filter((block) => block.id !== id))
  }

  const handleUpdateBlock = (id: string, updates: any) => {
    setTimeBlocks(timeBlocks.map((block) => (block.id === id ? { ...block, ...updates } : block)))
  }

  const previousMonth = () => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() - 1))
  }

  const nextMonth = () => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1))
  }

  const handleOpenModal = () => {
    setPresetBlock(null)
    setIsModalOpen(true)
  }

  const handleTimelineRangeSelect = (startHour: number, endHour: number) => {
    if (!selectedDate) return

    const pad = (n: number) => n.toString().padStart(2, "0")

    const startTime = `${pad(startHour)}:00`
    const endTime = `${pad(endHour)}:00`

    setPresetBlock({
      date: selectedDate.toISOString(),
      startTime,
      endTime,
    })
    setIsModalOpen(true)
  }

  const activeDate = selectedDate || new Date()

  const blocksForSelectedDate = timeBlocks.filter((block) =>
    isSameDay(new Date(block.date), activeDate)
  )

  return (
    <div className="min-h-screen flex bg-muted">
      {/* SIDEBAR */}
      <aside
        className={`relative hidden sm:flex flex-col bg-[#2f3a29] text-white transition-all duration-300 shadow-lg ${
          isSidebarOpen ? "w-64" : "w-20"
        }`}
      >
        {/* Toggle button */}
        <button
          onClick={() => setIsSidebarOpen((prev) => !prev)}
          className="absolute -right-3 top-6 z-10 h-6 w-6 rounded-full bg-card text-foreground flex items-center justify-center shadow-md border border-border"
        >
          <ChevronLeft
            size={16}
            className={`transition-transform ${!isSidebarOpen ? "rotate-180" : ""}`}
          />
        </button>

        {/* Brand */}
        <div className="px-4 pt-4 pb-6 border-b border-white/10 flex items-center gap-3">
          <div className="flex h-9 w-9 items-center justify-center rounded-lg bg-secondary text-secondary-foreground font-bold text-xs shadow-sm">
            SC
          </div>
          {isSidebarOpen && (
            <div className="flex flex-col">
              <span className="text-sm font-semibold tracking-tight">Smart Calendar</span>
              <span className="text-[11px] text-white/70">Time Blocking System</span>
            </div>
          )}
        </div>

        {/* Nav */}
        <nav className="flex-1 px-3 py-4 space-y-2 text-sm">
          <SidebarItem
            icon={<CalendarDays size={16} />}
            label="Calendar"
            active
            collapsed={!isSidebarOpen}
          />
          <SidebarItem
            icon={<ListTodo size={16} />}
            label="Weekly To-Do"
            collapsed={!isSidebarOpen}
          />
          <SidebarItem icon={<Inbox size={16} />} label="Inbox" collapsed={!isSidebarOpen} />
          <SidebarItem icon={<Bot size={16} />} label="AI Assistant" collapsed={!isSidebarOpen} />
          <SidebarItem icon={<User size={16} />} label="Profile" collapsed={!isSidebarOpen} />
          <SidebarItem
            icon={<SettingsIcon size={16} />}
            label="Settings"
            collapsed={!isSidebarOpen}
          />
        </nav>

        {/* Logout */}
        <div className="px-3 pb-4">
          <button className="w-full flex items-center gap-2 rounded-lg bg-red-900/40 hover:bg-red-800/60 text-red-200 text-sm px-3 py-2 transition-colors">
            <LogOut size={16} />
            {isSidebarOpen && <span>Logout</span>}
          </button>
        </div>
      </aside>

      {/* MOBILE SIDEBAR TOGGLER */}
      <button
        className="sm:hidden fixed left-3 top-3 z-20 rounded-full bg-card/90 border border-border p-2 shadow-md"
        onClick={() => setIsSidebarOpen((prev) => !prev)}
      >
        <Menu size={18} />
      </button>

      {/* MAIN CONTENT */}
      <main className="flex-1 px-4 lg:px-8 py-6 lg:py-8 transition-all duration-300">
        <div className="max-w-6xl mx-auto space-y-8">
          {/* Header */}
          <div className="flex flex-col lg:flex-row items-start lg:items-center justify-between gap-4">
            <div className="space-y-1">
              <h1 className="text-3xl font-bold text-foreground tracking-tight">Your Calendar</h1>
              <p className="text-muted-foreground">
                Plan your week with smart time blocking and 24-hour control.
              </p>
            </div>

            <button
              onClick={handleOpenModal}
              className="inline-flex items-center gap-2 bg-secondary hover:bg-secondary/90 text-secondary-foreground px-6 py-2.5 rounded-lg font-medium transition-all shadow-sm hover:shadow-md hover:-translate-y-0.5"
            >
              <Plus size={20} />
              Add Time Block
            </button>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 lg:gap-8">
            {/* Mini Calendar */}
            <div className="bg-card rounded-xl shadow-md p-5 border border-border lg:col-span-1 h-fit transition-all hover:shadow-lg hover:-translate-y-0.5">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-lg font-bold text-foreground">
                  {currentDate.toLocaleString("default", { month: "long", year: "numeric" })}
                </h2>
                <div className="flex gap-2">
                  <button
                    onClick={previousMonth}
                    className="p-1.5 hover:bg-muted rounded-lg transition-colors"
                  >
                    <ChevronLeft size={18} />
                  </button>
                  <button
                    onClick={nextMonth}
                    className="p-1.5 hover:bg-muted rounded-lg transition-colors"
                  >
                    <ChevronRight size={18} />
                  </button>
                </div>
              </div>

              <p className="text-xs text-muted-foreground mb-3">
                Busy days appear in red, lighter days in green.
              </p>

              <CalendarView
                currentDate={currentDate}
                selectedDate={selectedDate}
                onSelectDate={setSelectedDate}
                timeBlocks={timeBlocks}
              />
            </div>

            {/* Right column: schedule + tips */}
            <div className="lg:col-span-2 space-y-5">
              {/* This Week's Schedule + Day Timeline */}
              <div className="bg-card rounded-xl shadow-md p-6 border border-border transition-all hover:shadow-lg hover:-translate-y-0.5">
                <div className="flex items-center justify-between mb-4">
                  <div>
                    <h2 className="text-xl font-bold text-foreground">This Week&apos;s Schedule</h2>
                    <p className="text-xs text-muted-foreground mt-1">
                      Select a day on the calendar and drag on the timeline to block your time.
                    </p>
                  </div>
                  <div className="text-right text-xs text-muted-foreground hidden md:block">
                    <p>{formatDateLabel(activeDate)}</p>
                    <p className="mt-1">
                      {blocksForSelectedDate.length > 0
                        ? `${blocksForSelectedDate.length} block${
                            blocksForSelectedDate.length > 1 ? "s" : ""
                          } scheduled`
                        : "No blocks yet for this day"}
                    </p>
                  </div>
                </div>

                {/* 24h Day Timeline */}
                <DayTimeline
                  dayBlocks={blocksForSelectedDate}
                  onRangeSelect={handleTimelineRangeSelect}
                />

                {/* List of all blocks */}
                <div className="mt-6 space-y-3">
                  {timeBlocks.length === 0 ? (
                    <div className="text-center py-8">
                      <p className="text-muted-foreground">No time blocks scheduled yet</p>
                      <p className="text-sm text-muted-foreground mt-2">
                        Add your first time block to get started
                      </p>
                    </div>
                  ) : (
                    timeBlocks.map((block) => (
                      <div
                        key={block.id}
                        className="bg-gradient-to-r from-primary/10 to-secondary/10 border border-border rounded-lg p-4 flex items-center justify-between gap-4 hover:shadow-md transition-all hover:-translate-y-0.5"
                      >
                        <div>
                          <h3 className="font-bold text-foreground">{block.title}</h3>
                          <p className="text-sm text-muted-foreground">
                            {block.startTime} – {block.endTime} on{" "}
                            {new Date(block.date).toLocaleDateString()}
                          </p>
                          {block.description && (
                            <p className="text-sm text-foreground/80 mt-1">{block.description}</p>
                          )}
                        </div>
                        <button
                          onClick={() => handleDeleteBlock(block.id)}
                          className="text-destructive hover:text-destructive/80 font-medium text-sm"
                        >
                          Delete
                        </button>
                      </div>
                    ))
                  )}
                </div>
              </div>

              {/* Productivity Tips */}
              <div className="bg-gradient-to-br from-accent/20 to-secondary/10 rounded-xl shadow-md p-6 border border-accent/30 transition-all hover:shadow-lg hover:-translate-y-0.5">
                <h3 className="font-bold text-foreground mb-3">Time Blocking Tips</h3>
                <ul className="space-y-2 text-sm text-foreground/80">
                  <li>✓ Block 90 minutes for deep work sessions</li>
                  <li>✓ Include buffer time between meetings or classes</li>
                  <li>✓ Schedule 5–10 minute breaks every 2 hours</li>
                  <li>✓ Batch similar tasks together to reduce context switching</li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        {/* Modal */}
        {isModalOpen && (
          <TimeBlockingModal
            onClose={() => {
              setIsModalOpen(false)
              setPresetBlock(null)
            }}
            onAdd={handleAddBlock}
            // OPTIONAL: use these inside your modal to prefill form fields
            presetDate={presetBlock?.date}
            presetStartTime={presetBlock?.startTime}
            presetEndTime={presetBlock?.endTime}
          />
        )}
      </main>
    </div>
  )
}

/* ----------------- helpers & small components ----------------- */

function SidebarItem({
  icon,
  label,
  active,
  collapsed,
}: {
  icon: React.ReactNode
  label: string
  active?: boolean
  collapsed?: boolean
}) {
  return (
    <button
      className={`w-full flex items-center gap-3 rounded-lg px-3 py-2.5 text-left transition-all ${
        active
          ? "bg-[#c68a5a] text-white shadow-sm"
          : "text-white/80 hover:bg-white/10 hover:text-white"
      }`}
    >
      <span className="flex h-6 w-6 items-center justify-center">{icon}</span>
      {!collapsed && <span className="text-sm">{label}</span>}
    </button>
  )
}

function isSameDay(a: Date, b: Date) {
  return (
    a.getFullYear() === b.getFullYear() &&
    a.getMonth() === b.getMonth() &&
    a.getDate() === b.getDate()
  )
}

function formatDateLabel(date: Date) {
  return date.toLocaleDateString(undefined, {
    weekday: "short",
    day: "numeric",
    month: "short",
    year: "numeric",
  })
}

/**
 * 24-hour vertical timeline for a single day.
 * - Drag from one hour to another to select a block.
 * - Existing blocks for that day are shown as "busy" hours.
 */
function DayTimeline({
  dayBlocks,
  onRangeSelect,
}: {
  dayBlocks: TimeBlock[]
  onRangeSelect: (startHour: number, endHour: number) => void
}) {
  const HOURS = Array.from({ length: 24 }, (_, i) => i)

  const [isDragging, setIsDragging] = useState(false)
  const [dragStart, setDragStart] = useState<number | null>(null)
  const [dragEnd, setDragEnd] = useState<number | null>(null)

  // compute busy hours from existing blocks
  const busyHours = new Set<number>()
  dayBlocks.forEach((block) => {
    const [sh] = block.startTime.split(":").map(Number)
    const [eh] = block.endTime.split(":").map(Number)
    const start = Number.isFinite(sh) ? sh : 0
    const end = Number.isFinite(eh) ? eh : start + 1
    for (let h = start; h < end; h++) {
      busyHours.add(h)
    }
  })

  const handleMouseDown = (hour: number) => {
    setIsDragging(true)
    setDragStart(hour)
    setDragEnd(hour)
  }

  const handleMouseEnter = (hour: number) => {
    if (!isDragging) return
    setDragEnd(hour)
  }

  const finishDrag = () => {
    if (isDragging && dragStart !== null && dragEnd !== null) {
      const start = Math.min(dragStart, dragEnd)
      const end = Math.max(dragStart, dragEnd) + 1 // end-exclusive
      if (end > start) {
        onRangeSelect(start, end)
      }
    }
    setIsDragging(false)
    setDragStart(null)
    setDragEnd(null)
  }

  const isInSelection = (hour: number) => {
    if (dragStart === null || dragEnd === null) return false
    const start = Math.min(dragStart, dragEnd)
    const end = Math.max(dragStart, dragEnd)
    return hour >= start && hour <= end
  }

  return (
    <div
      className="rounded-xl border border-border bg-muted/40 p-3 max-h-80 overflow-y-auto transition-all"
      onMouseLeave={finishDrag}
      onMouseUp={finishDrag}
    >
      <div className="grid grid-cols-[60px_1fr] gap-2">
        {/* hour labels */}
        <div className="flex flex-col gap-1 text-xs text-muted-foreground">
          {HOURS.map((hour) => (
            <div key={hour} className="h-7 flex items-center justify-end pr-1">
              {hour.toString().padStart(2, "0")}:00
            </div>
          ))}
        </div>

        {/* slots */}
        <div className="flex flex-col gap-1">
          {HOURS.map((hour) => {
            const busy = busyHours.has(hour)
            const selected = isInSelection(hour)

            let classes =
              "h-7 rounded-lg border cursor-pointer transition-all duration-150 select-none"
            if (busy) {
              classes += " bg-destructive/20 border-destructive/60"
            } else {
              classes += " bg-background/80 border-border/60 hover:bg-secondary/20"
            }
            if (selected) {
              classes += " bg-secondary/50 border-secondary shadow-inner"
            }

            return (
              <div
                key={hour}
                onMouseDown={() => handleMouseDown(hour)}
                onMouseEnter={() => handleMouseEnter(hour)}
                className={classes}
              />
            )
          })}
        </div>
      </div>
    </div>
  )
}
