"use client"

interface CalendarViewProps {
  currentDate: Date
  selectedDate: Date | null
  onSelectDate: (date: Date) => void
  timeBlocks: any[]
}

export default function CalendarView({ currentDate, selectedDate, onSelectDate, timeBlocks }: CalendarViewProps) {
  const getDaysInMonth = (date: Date) => {
    return new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate()
  }

  const getFirstDayOfMonth = (date: Date) => {
    return new Date(date.getFullYear(), date.getMonth(), 1).getDay()
  }

  const daysInMonth = getDaysInMonth(currentDate)
  const firstDay = getFirstDayOfMonth(currentDate)
  const days = []

  // Empty cells for days before month starts
  for (let i = 0; i < firstDay; i++) {
    days.push(null)
  }

  // Days of the month
  for (let i = 1; i <= daysInMonth; i++) {
    days.push(new Date(currentDate.getFullYear(), currentDate.getMonth(), i))
  }

  const getBlocksForDate = (date: Date) => {
    return timeBlocks.filter((block) => {
      const blockDate = new Date(block.date)
      return (
        blockDate.getFullYear() === date.getFullYear() &&
        blockDate.getMonth() === date.getMonth() &&
        blockDate.getDate() === date.getDate()
      )
    })
  }

  return (
    <div>
      {/* Weekday headers */}
      <div className="grid grid-cols-7 gap-1 mb-2">
        {["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"].map((day) => (
          <div key={day} className="text-center text-xs font-bold text-muted-foreground p-2">
            {day}
          </div>
        ))}
      </div>

      {/* Calendar days */}
      <div className="grid grid-cols-7 gap-1">
        {days.map((day, index) => {
          const isToday = day && day.toDateString() === new Date().toDateString()
          const isSelected = day && selectedDate && day.toDateString() === selectedDate.toDateString()
          const blocksForDay = day ? getBlocksForDate(day) : []

          return (
            <div
              key={index}
              onClick={() => day && onSelectDate(day)}
              className={`aspect-square p-1 rounded-lg text-xs font-medium cursor-pointer transition-all ${
                day === null
                  ? "bg-transparent"
                  : isSelected
                    ? "bg-secondary text-secondary-foreground"
                    : isToday
                      ? "bg-accent text-accent-foreground border-2 border-secondary"
                      : "bg-muted/40 text-foreground hover:bg-muted/60"
              }`}
            >
              <div className="h-full flex flex-col items-center justify-center">
                {day && <span>{day.getDate()}</span>}
                {blocksForDay.length > 0 && <div className="w-1 h-1 bg-secondary rounded-full mt-0.5"></div>}
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}
